import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import { Alert } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Result from './panels/Result';

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	var dayss = [];
	var months = ['','января','февраля','марта','апреля','мая','июня','июля','августа','сентября','октября','ноября','декабря'];
	var years = [];
	for(var i=1;i<32;i++){
		dayss.push(<option value={i} key={i}>{i}</option>)
	}
	for(i=1;i<13;i++){
		months[i] = <option value={i} key={i}>{months[i]}</option>;
	}
	months.shift();
	for(i=new Date().getFullYear();i>1900;i--){
		years.push(<option value={i} key={i}>{i}</option>);
	}
	const [daySelected, setDay] = useState('0');
	const [monthSelected, setMonth] = useState('0');
	const [yearSelected, setYear] = useState('0');
	const [days, setDays] = useState(dayss);
	const [data, setData] = useState(null);
	const [ads,setAds] = useState(null);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		bridge.send('VKWebAppGetAuthToken',{'app_id':7659725,'scope':''}).then(data => {
			bridge.send('VKWebAppCallAPIMethod',{'method':'users.get','request_id':'123','params':{'fields':'bdate','v':'5.126','access_token':data.access_token}}).then(data => {
				setUser(data.response[0]);
				if(data.response[0].bdate !== undefined){
					var date = data.response[0].bdate.split('.');
					if(date[0] !== undefined) setDay(date[0]);
					if(date[1] !== undefined) setMonth(date[1]);
					if(date[2] !== undefined) setYear(date[2]);
				}
			}).catch(error => {
				console.log(error);
			});
			setPopout(null);
		});
		bridge.send('VKWebAppShowNativeAds',{'ad_format':'preloader'});
	}, []);


	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};
	
	const onc = e => {
		var id = e.currentTarget.id;
		var m = null;
		var y = null;
		switch(id){
			case 'day':
				setDay(e.currentTarget.value);
				break;
			case 'month':
				m = e.currentTarget.value;
				setMonth(e.currentTarget.value);
				break;
			case 'year':
				y = e.currentTarget.value;
				setYear(e.currentTarget.value);
				break;
			default:
				break;
		}
		m = (m === null) ? monthSelected : m;
		y = (y === null) ? yearSelected : y;
		if(m === 2 && y % 4 !== 0 || (y % 100 === 0 && y % 400 !== 0)) setDaysAmount(28)
		else if(m === 2 && y % 4 === 0 || (y % 100 !== 0 && y % 400 === 0)) setDaysAmount(29)
		else if(m < 8 && m % 2 === 0) setDaysAmount(30)
		else if(m < 8 && m % 2 !== 0) setDaysAmount(31)
		else if(m > 7 && m % 2 === 0) setDaysAmount(31)
		else if(m > 7 && m % 2 !== 0) setDaysAmount(30);
	}
	
	const setDaysAmount = a => {
		var dayss = [];
		for(var i=1;i<a+1;i++){
			dayss.push(<option value={i}>{i}</option>)
		}
		setDays(dayss);
	}
	
	const req = e => {
		setPopout(<ScreenSpinner size='large' />);
		bridge.send('VKWebAppGetAds',{}).then(ad => {
			setAds(ad);
		});
		var xhr = new XMLHttpRequest();
		xhr.open('GET',"https://vovadev.ru/pastlife/get.php?"+window.location.href.split('?')[1]+"&day="+daySelected+"&month="+monthSelected+"&year="+yearSelected);
		xhr.send();
		xhr.onprogress = function(){
			if(xhr.status === 200){
				var r = JSON.parse(xhr.response);
				if(r.error !== ''){
					setPopout(<Alert
								actionsLayout="vertical"
								actions={[{
								  title: 'ОК',
								  autoclose: true,
								  mode: 'cancel'
								}]}
								onClose={closePopout}
							  >
								<h2>Ошибка</h2>
								<p>{r.error}</p>
							  </Alert>);
				} else{
					setData(r.response);
					setActivePanel('result');
					setPopout(null);
				}
			}
		}
	}
	
	const closePopout = e => {
		setPopout(null);
	}
	
	const openStory = e => {
		var user = fetchedUser;
		var info = data.id;
		var url = 'https://vovadev.ru/pastlife/img.php?vkid='+user.id+'&id='+info;
		console.log(e.currentTarget.dataset);
		var sticker = {
			'content_type':'image',
			'url':'https://vovadev.ru/pastlife/button.php',
			'can_delete':false,
			'original_width':712,
			'original_height':161,
			'transform':{
				'rotation':0,
				'translation_x':0,
				'translation_y':-0.1,
				'gravity':'center_bottom'
			},
			'clickable_zones':[{
				'action_type':'app',
				'action':{
					'app_id':7659725
				},
				'clickable_area':[{
					'x':0,
					'y':0
				},
				{
					'x':0,
					'y':161
				},
				{
					'x':712,
					'y':161
				},
				{
					'x':712,
					'y':0
				}]
			}]
		};
		bridge.send('VKWebAppShowStoryBox',{'background_type':'image','url':url,'locked':true,'attachment':{'text':'view','type':'url','url':'https://vk.com/app7659725'},'stickers':[{'sticker_type':'renderable','sticker':sticker}]});
	}

	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' days={days} months={months} years={years} day={daySelected} month={monthSelected} year={yearSelected} onc={onc} req={req} />
			<Result id='result' go={go} result={data} openStory={openStory} ads={ads} />
		</View>
	);
}

export default App;

