import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import { Select, FormLayout } from '@vkontakte/vkui';

const Home = ({ id, days, months, years, day, month, year, onc, req }) => (
	<Panel id={id}>
		<PanelHeader>Кем ты был в прошлой жизни?</PanelHeader>
		<Div style={{color:'gray'}}>
		Узнайте, кем вы были в прошлой жизни! Введите дату своего рождения ниже и нажмите "Узнать".
		</Div>
		<FormLayout>
		  <Select top="День" id="day" value={day} onChange={onc} placeholder="Выберите день">
		  {days}
		  </Select>
		  <Select top="Месяц" id="month" value={month} placeholder="Выберите месяц" onChange={onc} data-day="29">
		  {months}
		  </Select>
		  <Select top="Год" id="year" value={year} onChange={onc} placeholder="Выберите год">
		  {years}
		  </Select>
		  <Button size="xl" mode="primary" onClick={req}>Узнать</Button>
		</FormLayout>
	</Panel>
);

export default Home;
