import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Story from '@vkontakte/icons/dist/24/story';
import { Group,Div,Header,Button,PromoBanner } from '@vkontakte/vkui';

const Result = props => (
	<Panel id={props.id}>
		<PanelHeader>Результат</PanelHeader>
		<Group header={<Header mode="secondary">Пол:</Header>}>
			<Div>
			{props.result.sex}
			</Div>
		</Group>
		<Group header={<Header mode="secondary">Год рождения:</Header>}>
			<Div>
			{props.result.birth}
			</Div>
		</Group>
		<Group header={<Header mode="secondary">Родина:</Header>}>
			<Div>
			{props.result.home}
			</Div>
		</Group>
		<Group header={<Header mode="secondary">Род деятельности:</Header>}>
			<Div>
			{props.result.prof}
			</Div>
		</Group>
		<Group header={<Header mode="secondary">Характер:</Header>}>
			<Div>
			{props.result.harak}
			</Div>
		</Group>
		<Group header={<Header mode="secondary">Причина смерти:</Header>}>
			<Div>
			{props.result.death}
			</Div>
		</Group>
		{ props.ads != null && <PromoBanner bannerData={props.ads} />}
		<Group>
			<Div style={{display:'flex'}}>
				<Button size="l" stretched mode="secondary" style={{ marginRight: 8 }} before={<Icon24Cancel /> } onClick={props.go} data-to="home">Закрыть</Button>
				<Button size="l" stretched mode="primary" before={<Icon24Story />} onClick={props.openStory} data-user={props.user} data-result={props.result}>Поделиться</Button>
			</Div>
		</Group>
	</Panel>
);

export default Result;
